  }

  &__link {
      display: flex;
      align-items: center;
      min-height: 13px;
      padding-right: 25px;
      padding-left: 25px;      
      border-right: 1px solid #fff;
      &:last-child {
          border-right: none;
      }
      a {
        font-size: 12px;
        color: #fff;
        &:hover {
            color: #e3e3e3;
            transition: color 0.05s linear;
        }
      }
  }

  &__copy {
      margin-top: 19px;
      text-align: center;
      color: #fff;
      font-size: 12px;
  }
}

