    // custom select
    /* Reset Select */
select {
  -webkit-appearance: none;
  -moz-appearance: none;
  -ms-appearance: none;
  appearance: none;
  outline: 0;
  box-shadow: none;
  border: 0 !important;
  background: #db1e27;
  background-image: none;
}
/* Custom Select */
.table__select-hotness {
    display: inline-block;
   width: 82px;
  height: 28px;
  line-height: 28px;
}
.select{
  position: relative;
  display: block;
  margin: 0 auto;
  width: 100%;
  max-width: 65px;
  color: #fff;
  text-align: left;
  user-select: none;
  -webkit-touch-callout: none;

  .placeholder{
    position: relative;
    display: block;
    z-index: 1;
    height:28px;
    line-height: 28px;
    padding-left: 15px;
    padding-right: 15px;
    border-radius: 3px;
    background-color: #aaa;
    font-weight: 700;
    cursor: pointer;

    &:after{
      position: absolute;
      right: -12px;
      top: 50%;
      transform: translateY(-50%);
      color: #333333;
      font-family: 'FontAwesome';
      content: '\f0d7';
      z-index: 10;
    }
  }

  &.is-open{
    .placeholder:after{
      content: '\f0d8';
    }
    ul{
      display: block;
    }
  }

  ul{
    display: none;
    position: absolute;
    width: 100%;
    background: #eee;
    border-radius: 3px;
    top: 100%;
    left: 0;
    list-style: none;
    margin: 5px 0 0 0;
    padding: 0;
    z-index: 100;
    border-radius: 3px;
    

    li{
      display: block;
      text-align: left;
      padding-left: 18px;
      color: #333;
      font-weight: bold;
      cursor: pointer;

      &:hover{
        background: #4ebbf0;
        color: #fff;
      }
    }
  }
}
// end custom select
