    }
}

// SHOWING REQUESTS PAGE
.main--requests {
    .main__table {
        font-size: 14px;
    }
    .tablenav__search-box {
        display: flex;
        align-items: center;

        .tablenav__search-field {
        font-size: 16px;
        height: 32px;
        padding-left: 14px;
        padding-right: 50px;
        color: #444444; 
        background-color: #ffffff;
        border: solid 1px #dfdfdf;
        }

        .tablenav__search-button {
        width: 46px;
        height: 32px;
        margin-left: -2px;
        font-size: 16px;
        color: #fff;
        border: none;
        background-color: $ugly-blue;
        }
    }

        .table__username {
            font-size: 20px;
            font-weight: bold;
            color: $ugly-blue;
        }
    .table__status-cell {
        .table__status {
            display: inline-block;
            margin-right: 9px;
        }
        .btn--agentname {
            height: 28px;
            padding: 5px 20px;  
            line-height: 0;
            color: $ugly-blue;
            background-color: #ffffff;
            border: solid 1px #dfdfdf;
        }
        @media only screen and (max-width : 968px) {
            .btn--agentname {
                display: block;
                margin: 5px 0 5px 0;
                padding: 5px 10px; 
            }
            .btn {
                height: 15px;
                line-height: 15px;
            }
        }
        .btn--agentname--disabled {
            display: none;
        }
        .table__status-btn {
            float: right;
            position: relative;
            margin-right: 12px;
            cursor: pointer;
                
            i {
                font-size: 22px;
            }
            &:hover .table__status-popup{
               color: $ugly-blue; 
            }
            &:hover .table__status-popup{
               display: block;
               &:after, &:before {
                bottom: 100%;
                right: 5px;
                border: solid transparent;
                content: " ";
                height: 0;
                width: 0;
                position: absolute;
                pointer-events: none;
            }

            &:after {
                border-bottom-color: #fff;
                border-width: 8px;
                margin-left: -8px;
            }
            &:before {
                border-bottom-color: rgba(2, 3, 3, 0.7);
                border-width: 8px;
                margin-left: -8px;
            } 
            }
            
            .table__status-popup {
                display: none;
                position: absolute;
                right: -6px;                
                padding: 7px 6px;
                z-index: 10;
                background-color: #ffffff;
                box-shadow: 0 0 7px 0 rgba(2, 3, 3, 0.23);
                border-radius: 3px;
            }
            .status__radio {
                display: block;
                width: 142px;
                height: 30px;
                line-height: 30px;
                border-bottom: 1px solid #dfdfdf;
                &:last-child {
                    border-bottom: none;
                }
                .table__status-name {
                    display: inline-block;
                    width: 100%;
                    padding-left: 17px;
                    font-size: 16px;
                    color: $text-dark;
                    box-sizing: border-box;
                }
                .table__status-name:hover {
                    color: #fff;
                    background-color: $ugly-blue;
                    cursor: pointer;

                }
            }
            &:after {
                content: "";
                display: table;
                clear: both;
            }
        }
    }
}

